<template>
  <div class="news">
    <div class="banner">
      <img src="../../common/images/news01.jpg"/>
    </div>


    <div class="news-box">
      <div class="width-common">
        <h1>{{detail.title}}</h1>
        <div class="news-des">{{detail.author}} | {{detail.create_time}} | 来源：{{detail.source_from}}

        </div>
        <div class="news-cont" v-html="detail.detail">

        </div>

      </div>

    </div>

    <div class="news-read">
      <div class="width-common">
        <h2>相关阅读</h2>
        <ul>
          <li v-for="item in detail_list " @click="goNews(item.id)">
            {{item.title}} <span class="time">{{item.create_time}}</span>
          </li>
        </ul>
      </div>
    </div>



  </div>
</template>
<script>
  import {PopAlert, ShowLoading, HideLoading} from 'common/js/mylayer'
  import {URL_BASE} from 'common/js/public'
  import {MyPost} from 'common/js/request'
  export default {
    data(){
      return{
        detail:{},
        id:'',
        detail_list:[],
      }
    },
    created(){
      if(this.$route.query.id){
        this.id= this.$route.query.id
      }
      console.log(this.id)
      this.getNews()
    },
    methods:{
      getNews() { // 初始请求
        MyPost(URL_BASE+'/index.php/api/news/getDetail',
          {
            id:this.id
          }).then(res => {
          console.log(res)
          if (res.return_code === 0) {
            this.detail = res.data;
            this.detail_list = res.data.relation_news
          }else{
            PopAlert(res.return_message)
          }
        });

      },
      goNews(id){
        this.id =id;
        this.getNews()
        document.documentElement.scrollTop = document.body.scrollTop =0;
      }
    }

  }

</script>
<style lang="stylus" rel="stylesheet/stylus" scoped>
.news
  color #676767
  .banner
    margin-bottom 80px
    img
      width 100%
      display block

  .news-box
    h1
      font-size 36px;
      line-height 60px
      color #252525
      text-align center
      padding-top 70px
      border-top 1px solid #f1f1f1
    .news-des
      font-size 24px
      line-height 36px
      padding-bottom 20px
      text-align center
    .news-cont
      font-size 24px
      line-height 36px
      margin-bottom 100px
      img
        text-align center
  .news-read
    margin-top 40px
    h2
      font-size 36px
      padding 20px 0
      border-bottom 1px solid #f1f1f1
    ul
      padding-bottom 90px
      padding-top 20px
      li
        line-height 52px
        display block
        font-size 24px
        cursor pointer
        background url("../../common/images/libg.png") no-repeat 0 center;
        background-size 14px
        padding-left 30px
        .time
          display inline-block
          margin-left 20px





</style>





